import {library} from '@fortawesome/fontawesome-svg-core'

import {faClock} from '@fortawesome/free-regular-svg-icons'

import {required, requiredIf} from "vuelidate/lib/validators";
import timing from "./user-panel/timing";

// import {required, numeric, minLength, maxLength} from "vuelidate/lib/validators";


library.add(faClock)


export default {
  middleware: 'auth',
  name: "tutor-timing",
  data() {
    return {
      nextPush: "/tutor-finish",
      busy: true,
      week: ['MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT', 'SUN'],
      timing: [],
      clock: []
    }
  },
  mounted() {
    for (let i = 0; i < 48; i++)
      this.clock.push(i)
    this.week.forEach(() => {
      const sArr = [];
      this.clock.forEach(function (c) {
        sArr[c] = false
      });
      this.timing.push(sArr)
    });
    this.loadTiming()
  },
  methods: {
    hideOverlay()
    {
      this.busy = false
    },
    showOverlay()
    {
      this.busy = true
    },
    loadTiming() {
      try{
        this.showOverlay()
        this.$axios.post('/timing/getTiming').then((res) => {
          res.data.forEach((item) => {
            this.$set(this.timing[item.dayOfWeek], item.timeStart, true);
          })
        }).catch(error => {
          console.log(error.response)
        }).finally(()=>{
          this.hideOverlay()
        })
      }catch (e) {
        this.hideOverlay()
      }
    },

    nextStep(){
      let flag = false
      for (let clockIndex = 0; clockIndex < 48; clockIndex++) {
        for (let w = 0; w < 7; w++) {
          if(this.timing[w][clockIndex] === true)
          {
            flag = true
            break
          }
        }
        if(flag)
          break
      }

      if (!flag) {
        this.$bvModal.msgBoxConfirm(this.$t('schedule.empty_alert'), {
          title: 'Confirm',
          size: 'md',
          buttonSize: 'sm',
          okVariant: 'danger',
          okTitle: 'YES',
          cancelTitle: 'NO',
          footerClass: 'p-2 border-top-0',
          hideHeaderClose: false,
          headerClass: 'p-2 border-bottom-0',
          centered: true
        })
          .then(value => {
            if (value === true) {
              this.$router.push(this.nextPush)
            }
          })
          .catch(err => {
          })
      }
      else {
        this.$router.push(this.nextPush)
      }
    }
    ,

    changer(it, id) {
      if (id === this.clock.length - 1)
        return
      if (this.timing[it][id + 1])
        return
      if (id < 0)
        return

      this.$set(this.timing[it], id, !this.timing[it][id]);
      this.saveToTiming(it, id, !this.timing[it][id])
    },

    saveToTiming(day, start, checked) {
      let formData = {
        checked: checked,
        day: day,
        start: start
      }
      this.$axios.post('/timing/save-tutor-timing', formData).then((res) => {

      }).catch(error => {

      })
    },


    makeToast(variant = 'success', header = 'Message', msg = '') {
      this.$bvToast.toast(this.$t(msg), {
        title: header,
        variant: variant,
        solid: true
      })
    },

    onReset(evt) {
      evt.preventDefault()
      // Reset our registerForm values


    },
  },
}
